@font-face {
    font-family: 'Museo Cyrl';
    src: local('Museo Cyrl 500'), local('MuseoCyrl-500'), url('museo-cyrl-500/Museocyrl500.woff2') format('woff2'), url('museo-cyrl-500/Museocyrl500.woff') format('woff'), url('museo-cyrl-500/Museocyrl500.ttf') format('truetype');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'Museo Cyrl';
    src: local('Museo Cyrl 700'), local('MuseoCyrl-700'), url('museo-cyrl-700/Museocyrl700.woff2') format('woff2'), url('museo-cyrl-700/Museocyrl700.woff') format('woff'), url('museo-cyrl-700/Museocyrl700.ttf') format('truetype');
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'Circe Rounded';
    src: local('Circe Rounded Regular'), local('Circe-Rounded-Regular'),
    url('circe-rounded-400/circe-rounded-400.woff2') format('woff2'),
    url('circe-rounded-400/circe-rounded-400.woff') format('woff'),
    url('circe-rounded-400/circe-rounded-400.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}